






























































































.ant-carousel {
  /deep/ .ant-row:not(:first-of-type) {
    padding-top: 24px;
  }
  /deep/ .custom-slick-arrow {
    width: 24px;
    height: 24px;
    font-size: 24px;
    color: #1890ff;
    opacity: 1;
    &:before {
      display: none;
    }
    &:hover {
      opacity: 0.5;
    }
  }
}

